
























export default {
	data() {
		return {
			uploadimg:'',

			tasksDetails:{
				taskTitle:'',
				copyWriting:'',
				picId:'',
				picId2:'',
				taskIdentifier:"",//任务编号
				statusTask:''//任务状态
			},
			copyImages:[],
			orderInfo:{
				taskIdentifier:'',
				account:'',
				orderNo:''
			},
			order:{}
			
		};
	},
	onLoad(options){
		 let order = JSON.parse(options.task);
		 this.tasksDetails.taskTitle = order.taskInfo.taskTitle;
		 this.orderInfo.taskIdentifier = order.taskInfo.taskIdentifier; //编号
		 this.orderInfo.orderNo = order.orderNo;
		 this.orderInfo.account = uni.getStorageSync('userInfo').account;
	},
	methods:{
		
		chooseImg() {
			var that = this;
			//选择图片
			uni.chooseImage({
				sourceType: ['camera', 'album'],
				sizeType: 'compressed',
				count: 1,
				success: function(res) { 
					uni.request({
						url:res.tempFilePaths[0],
						method:'GET',
						responseType: 'arraybuffer',
						success: ress => {
							var base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
							// base64 = 'data:image/jpeg;base64,' + base64 //不加上这串字符，在页面无法显示的哦
							// that.base64 = base64;
							const jsonString = {
								taskInfo:{
									picBast64:base64,
									upType:'pic',
								},
								requestType:"upFile",
							}
							const param ={
								controllerRequestType:"taskControllerService",
								jsonString:JSON.stringify(jsonString)
							}
							uni.request({
								url:that.websiteUrl,
								method:'POST',
								header: {
									"content-type":"application/x-www-form-urlencoded"
								},
								data:param,
								success: (res) => {
									if(res.data.errorCode == '0000'){
										that.picId.push(res.data.taskInfo.picId);
									}
								},
								fail:(res) =>{
									console.log(JSON.stringify(res));
								}
							})
							
						}
					})
						that.uploadimg = res.tempFilePaths[0];
						//触发所选择文件更改事件，参数为文件列表
						that.$emit('change', that.uploadimg);
				}
			});
			},
		previewImage(){
			//预览图片
			uni.previewImage({
				urls: [this.uploadimg]
			});
		},
		removeImage(img) {
			var that = this;
			uni.showModal({
				content: '确认删除选中的图片？',
				success: function() {
					 that.uploadimg = '';
				}
			});
		},
		getTask(){
			const jsonString = {
				orderInfo:this.orderInfo,
				requestType:"finishOrde",
			}
			const param ={
				controllerRequestType:"orderControllerService",
				jsonString:JSON.stringify(jsonString)
			}
			uni.request({
				url:this.websiteUrl,
				method:'POST',
				header: {
					"content-type":"application/x-www-form-urlencoded"
				},
				data:param,
				success(res) {
					console.log(res.data);
					if(res.data.errorCode =='0000'){
						uni.showModal({
							content:'您已经提交任务,请等待管理员审核',
							
							success(res) {
								if(res.confirm){
									
								}
									
								if(res.cancel){
								}
							}
						})
						
					}else{
						uni.showToast({
							icon:'none',
							title:res.data.errorMessage
						})
					}
				},
				fail(res) {
				uni.showToast({
					icon:'none',
					title:'网络异常！请稍后重试'
				})	
				}
			})	
			
			
				
		}
		
	}		
}



